'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './coupon.events';

var CouponSchema = new mongoose.Schema({
 name:{
 	type:String
 },
 offerPercentage:{
 	type:Number
 },
 createdAt:{
 	type:Date,
 	default:Date.now
 }
});

registerEvents(CouponSchema);
export default mongoose.model('Coupon', CouponSchema);
